import helper from '../../common/helper.js';
import uniSegmentedControl from "@/components/uni-segmented-control.vue"
export default {
	components: {
		uniSegmentedControl
	},
	data() {
		return {
			items: ['待领取', '已领取', '待审核', '已完成'],
			platformArray: ['全部','大众点评', '微信','QQ','抖音', '知乎', '微博', '小红书'],
			typeArray:[],
			platform:0,
			type:0,
			current: 0,
			rwlistArray: [], 
			Tishi: '',
			num: '9', 
			bottomtisi: '',
		}
	},
	onLoad() {
		 this.requestTask(this.current,this.type,5)
	},
	onShow() {
		// this.requestTask()
	},
	onPullDownRefresh: function() {
		this.requestTask(this.current,this.type,this.num)
		uni.showLoading({
			title: "刷新中....",
			mask: true
		})
	},
	//触底事件
	onReachBottom() {
		var tas1 = 5; //触底加载5条数据
		this.num = Number(this.num) + Number(tas1);
		this.requestTask(this.current,this.type,this.num);
	},
	methods: {
		//获取type
		optionPlatform(e){
			this.platform=e
			const that=this
			if (e===0){
				that.typeArray=[]
			    that.requestTask(this.current,0,9)
				return
			} 
			uni.request({
				url:helper.websiteUrl+"yiru/sdl/mertask/getbyplan",
				method:'POST',
				data:helper.postdata({
				plan:e
				}),
				success(res){
				
					if(res.data.code===200){
						that.typeArray=res.data.data.baseinfo
					}
				}
				
			})
		},
		optionType(type){
			this.type=type
			this.requestTask(this.current,type,this.num)
		},
		//--------------------------
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
			this.requestTask(this.current,index,this.num)
		},
		// 请求活动列表
		requestTask(current,type,num) {
			const that=this
			uni.request({
				url: helper.websiteUrl + 'yiru/sdl/mertask/tasklist',
				method: 'POST',
				data: helper.postdata({
					"plan":that.platform,
					"page":"1",
					"num":num,
					"MId":helper.getstate().userid,
					"cate":type,
					"sate":current+1
				}),
				success(res) {
					if (res.data.code == 200) {
						that.rwlistArray = res.data.data.tasklist;
						
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
					if (res.data.data.tasklist.length >= that.num) {
						that.bottomtisi = '正在加载...'
					} else {
						that.bottomtisi = '没有更多数据了'
					}
					if (res.data.data.tasklist.length === 0) { 
						that.Tishi = "暂无活动";
						that.bottomtisi = ''
					} else {
						that.Tishi = "";
					}
					uni.hideLoading();
				},
				fail() {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常，请下拉刷新'
					});
				}
			});
		},
		//获取待领取活动的id
		openlq(e) {
			uni.navigateTo({
				url: 'ToReceive?rwid=' + e.currentTarget.dataset.rwid
			});
		},
		//获取全部活动的id
		openqb(e) {
			uni.navigateTo({
				url: 'Taskdetail?rwid=' + e.currentTarget.dataset.rwid
			});
		},
		//获取已领取活动的id
		openDe(e) {
			console.log(e)
		},
		//获取已完成活动的id
		openDone(e) {
			uni.navigateTo({
				url: 'TaskDone?rwid=' + e.currentTarget.dataset.rwid
			});
		},
		//打开商家活动
		openindex() {
			uni.redirectTo({
				url: 'MerIndex'
			});
		},
		// 打开首页
		openInfo() {
			uni.redirectTo({
				url: 'MerInfo'
			});
		}

	}
}
